import Vue from 'vue';
import axios from 'axios';

axios.defaults.withCredentials = true;
Vue.prototype.$http = axios;

Vue.prototype.$api = function (apiName, postData, successFunc, errorFunc, isFile = false, isCheckLogin = true) {
    const _self = this;
    const apiList = _self.$C('apiList', 'api');
    if (apiList && apiList.hasOwnProperty(apiName)) {
        let url = _self.$C('apiPath', 'api') + apiList[apiName];

        //修改，不管是不是异步，都统一请求js的
        url = _self.$C('serverUrlAjax', 'sys') + url;

        const webID = _self.$C('webID');
        let config = {
            headers: {
                'X-LANG': _self.$lang(),
                'X-TOKEN': _self.$accessToken(),
                'X-BROWSER': _self.$browser(),
                'X-WEB-ID': webID ? webID : 0,
                'X-PUBLISH': _self.$isPublish(),
                'X-OEM': _self.$oem(),
                'X-RE': _self.$serverUrl(),
            }
        };
        if (isFile) {
            config.headers['Content-Type'] = 'multipart/form-data';
        }
        _self.$http.post(
            url,
            isFile ? postData : _self.$qs.stringify(postData),
            config
        ).then(function (response) {
            let re_data = response.data;
            let re_header = response.headers;

            //判断是否需要更新webid
            let VueUserCenterTruePublish = localStorage.getItem('VueUserCenterTruePublish');
            if(!VueUserCenterTruePublish || VueUserCenterTruePublish !== 'true'){
                //非正式发布状态下
                if (re_header['x-web-id'] || re_header['X-WEB-ID']) {
                    let web_id = re_header['x-web-id'] ? re_header['x-web-id'] : re_header['X-WEB-ID'];
                    web_id = parseInt(web_id);
                    if (!isNaN(web_id)) {
                        _self.$setConfig('web_id',web_id);
                    }
                }
            }

            if (re_header['X-RE'] || re_header['x-re']) {
                _self.$serverUrl(re_header['x-re'] ? re_header['x-re'] : re_header['X-RE']);
            }
            if (re_header['X-OEM'] || re_header['x-oem']) {
                _self.$oem(re_header['x-re'] ? re_header['x-re'] == 1 : re_header['X-RE'] == 1 );
            }
            if (re_data.status && re_data.status === 1) {
                if(re_data.body && re_data.body.flag === -1 && isCheckLogin){
                    _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                }else if (successFunc && typeof successFunc === "function") {
                    successFunc(re_data.body);
                }
            } else {
                if (errorFunc && typeof errorFunc === "function") {
                    errorFunc(re_data);
                }
            }
        }).catch(function (error) {
            if (error.toString().indexOf('Network Error') >= 0) {
                //这个是网络请求的错误
                if (errorFunc && typeof errorFunc === "function") {
                    errorFunc(error);
                }
            } else {
                console.log(error);
            }
        });
    } else {
        //api不存在
        if (errorFunc && typeof errorFunc === "function") {
            errorFunc(_self.$L('API接口错误'));
        }
    }
};

Vue.prototype.$getApi = function (apiName, postData, successFunc, errorFunc, isFile = false, isCheckLogin = true) {
    const _self = this;
    const apiList = _self.$C('apiList', 'api');
    if (apiList && apiList.hasOwnProperty(apiName)) {
        let url = _self.$C('apiPath', 'api') + apiList[apiName];

        //修改，不管是不是异步，都统一请求js的
        url = _self.$C('serverUrlAjax', 'sys') + url;

        const webID = _self.$C('webID');
        let config = {
            headers: {
                'X-LANG': _self.$lang(),
                'X-TOKEN': _self.$accessToken(),
                'X-BROWSER': _self.$browser(),
                'X-WEB-ID': webID ? webID : 0,
                'X-PUBLISH': _self.$isPublish(),
                'X-OEM': _self.$oem(),
                'X-RE': _self.$serverUrl(),
            }
        };
        if (isFile) {
            config.headers['Content-Type'] = 'multipart/form-data';
        }
        _self.$http.post(
            url,
            isFile ? postData : _self.$qs.stringify(postData),
            config
        ).then(function (response) {
            let re_data = response.data;
            let re_header = response.headers;

            //判断是否需要更新webid
            let VueUserCenterTruePublish = localStorage.getItem('VueUserCenterTruePublish');
            if(!VueUserCenterTruePublish || VueUserCenterTruePublish !== 'true'){
                //非正式发布状态下
                if (re_header['x-web-id'] || re_header['X-WEB-ID']) {
                    let web_id = re_header['x-web-id'] ? re_header['x-web-id'] : re_header['X-WEB-ID'];
                    web_id = parseInt(web_id);
                    if (!isNaN(web_id)) {
                        _self.$setConfig('web_id',web_id);
                    }
                }
            }

            if (re_header['X-RE'] || re_header['x-re']) {
                _self.$serverUrl(re_header['x-re'] ? re_header['x-re'] : re_header['X-RE']);
            }
            if (re_header['X-OEM'] || re_header['x-oem']) {
                _self.$oem(re_header['x-re'] ? re_header['x-re'] == 1 : re_header['X-RE'] == 1 );
            }
            if (re_data.status && re_data.status === 1) {
                if(re_data.body && re_data.body.flag === 1){
                    if (successFunc && typeof successFunc === "function") {
                        successFunc(re_data.body.data);
                    }
                }else if(re_data.body.flag === -1 && isCheckLogin){
                    _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                }else{
                    if (errorFunc && typeof errorFunc === "function") {
                        errorFunc(re_data.body);
                    }
                }
            } else {
                if (errorFunc && typeof errorFunc === "function") {
                    errorFunc({
                        flag:0,
                        msg:_self.$L('数据加载失败'),
                        data:re_data
                    });
                }
            }
        }).catch(function (error) {
            if (error.toString().indexOf('Network Error') >= 0) {
                //这个是网络请求的错误
                if (errorFunc && typeof errorFunc === "function") {
                    errorFunc({
                        flag:0,
                        msg:_self.$L('数据加载失败'),
                        data:error
                    });
                }
            } else {
                console.log(error);
            }
        });
    } else {
        //api不存在
        if (errorFunc && typeof errorFunc === "function") {
            errorFunc({
                flag:0,
                msg:_self.$L('API接口错误'),
            });
        }
    }
};