import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//公共样式
import './assets/css/common/common.scss'
import './assets/icon/iconfont.css'
//插件
// import './plugins/element.js'
import './plugins/comm.js'
import './plugins/func.js'
import './plugins/qs.js'
import './plugins/cookies.js'
import './plugins/vueMetaInfo.js'
import './plugins/vuescroll.js'
import './plugins/axios.js'
import './plugins/editor.js'
import './plugins/calendar.js'
import './plugins/html2canvas.js'
import Element from 'element-ui'
import zhLocale from 'element-ui/src/locale/lang/zh-CN.js'
import twLocale from 'element-ui/src/locale/lang/zh-TW.js'
import enLocale from 'element-ui/src/locale/lang/en.js'
import './element-variables.scss'
import VueI18n from 'vue-i18n'

Vue.config.productionTip = false;

const messages = {
  en: {
    ...enLocale
  },
  tw: {
    ...twLocale
  },
  zh: {
    ...zhLocale
  }
};
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: 'zh', // set locale
  messages, // set locale messages
});

Vue.use(Element,{
  i18n: (key, value) => i18n.t(key, value)
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app');
