import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        serverSystemConfig: {},
        serverLangConfig: {},
        seoIcon: null,
        seoTitle: null,
        mobileTitle: '',
        mobileTopBtn:null,
        routerData: {},
        loginConfig: {},
        dialogObj:null,
        heartbeatData:{}
    },
    mutations: {
        setMobileTopBtn(state, val){
            state.mobileTopBtn = val;
        },
        setMobileTitle(state, title){
            state.mobileTitle = title;
        },
        setHeartbeatData(state, data){
            let temp = {};
            temp = state.heartbeatData;
            state.heartbeatData = {};
            if (data) {
                for (let key in data) {
                    temp[key] = data[key];
                }
            }
            state.heartbeatData = temp;
        },
        setDialogObj(state, obj){
            state.dialogObj = null;
            state.dialogObj = obj;
        },
        setLoginConfig(state, config) {
            let temp = {};
            temp = state.loginConfig;
            state.loginConfig = {};
            if (config) {
                for (let key in config) {
                    temp[key] = config[key];
                }
            }
            state.loginConfig = temp;
        },
        setServerConfig(state, config) {
            if (config && config.hasOwnProperty('systemConfig')) {
                let serverSystemConfigTemp = {};
                serverSystemConfigTemp = state.serverSystemConfig;
                state.serverSystemConfig = {};
                for (let key in config.systemConfig) {
                    serverSystemConfigTemp[key] = config.systemConfig[key];
                }
                state.serverSystemConfig = serverSystemConfigTemp;
            }
            if (config && config.hasOwnProperty('langConfig')) {
                let langConfigTemp = {};
                langConfigTemp = state.serverLangConfig;
                state.serverLangConfig = {};
                for (let key in config.langConfig) {
                    langConfigTemp[key] = config.langConfig[key];
                }
                state.serverLangConfig = langConfigTemp;
            }
        },
        setRouterData(state, data) {
            if (data.hasOwnProperty('name') && data.hasOwnProperty('val')) {
                let temp = {};
                temp = state.routerData;
                state.routerData = {};
                temp[data.name] = data.val;
                state.routerData = temp;
            }
        },
        changeSeo(state, config) {
            if (config.hasOwnProperty('seoIcon')) {
                state.seoIcon = config.seoIcon;
            }
            if (config.hasOwnProperty('seoTitle')) {
                state.seoTitle = config.seoTitle;
            }
        }
    },
    actions: {}
})
