<template>
    <div id="app" v-loading="!isSetup">
        <router-view v-if="isSetup"></router-view>
        <div class="dialog-body show" v-if="dialogObj" :class="{ hide:dialogClose }">
            <div class="dialog-box show-box"
                 :class="{mobile:$isMobile()}"
                 :style="{width:dialogObj.hasOwnProperty('width')?dialogObj.width:'900px'}">
                <div class="dialog-title">
                    <span class="title-txt">{{ dialogObj.name }}</span>
                    <div class="title-close mainFontColorHover" @click="closeDialog">
                        <span class="bbx-font bbx-icon-close"></span>
                    </div>
                </div>
                <div class="dialog-obj">
                    <vue-scroll>
                        <component :is="dialogObj.view" @closeDialog="closeDialog"
                                   @closeDialogLoading="closeDialogLoading"
                                   @showDialogLoading="showDialogLoading"
                                   @setDialogHeight="setDialogHeight"
                                   :listener="listener"
                                   :data="dialogObj.hasOwnProperty('data')?dialogObj.data:{}"
                                   :height="dialogObj.hasOwnProperty('height')?dialogObj.height:null"
                                   :loading="dialogObj.hasOwnProperty('loading')?dialogObj.loading:false"></component>
                    </vue-scroll>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'app',
        data() {
            return {
                isSetup: false,
                loading: null,
                dialogClose: false,
                listener: null
            };
        },
        provide() {
            return {
                reload: this.reload,
                reloadSetup: this.reloadSetup,
            }
        },
        computed: {
            seoIcon: {
                get() {
                    return this.$store.state.seoIcon;
                },
                set(val) {
                    this.$store.commit('changeSeo', {seoIcon: val});
                }
            },
            seoTitle: {
                get() {
                    return this.$store.state.seoTitle;
                },
                set(val) {
                    this.$store.commit('changeSeo', {seoTitle: val});
                }
            },
            dialogObj: {
                get() {
                    if (this.$store.state.dialogObj) {
                        this.dialogClose = false;
                    }
                    return this.$store.state.dialogObj;
                },
                set(obj) {
                    if (obj && obj.hasOwnProperty("update")) {
                        this.$store.commit("setDialogObj", obj);
                    } else {
                        this.dialogClose = true;
                        setTimeout(() => {
                            this.$store.commit("setDialogObj", obj);
                        }, 500);
                    }
                }
            }
        },
        metaInfo() {
            let seo = {};
            if (this.seoTitle && this.seoTitle !== '') {
                seo.title = this.seoTitle;
            }
            if (this.seoIcon && this.seoIcon !== '') {
                seo.link = [{
                    rel: 'shortcut icon',
                    type: 'image/x-icon',
                    href: this.seoIcon
                }];
            }
            return seo;
        },
        created() {
            this.doSetup(true);
            window.addEventListener('message', (event) => {
                if (event.data.hasOwnProperty('order')) {
                    this.dialogListen(event.data.order, event.data.val);
                }
            });
        },
        methods: {
            reload() {
                this.isSetup = false;
                this.$nextTick(function () {
                    this.isSetup = true;
                })
            },
            reloadSetup(func){
                this.doSetup(true,func);
            },
            dialogListen(order, data) {
                this.listener = {
                    order: order,
                    data: data
                };
                switch (order) {
                    case "gotoHome":
                        this.closeDialog();
                        this.$gotoWebHome();
                        break;
                    case "showMsg":
                        this.$message({
                            type: data.hasOwnProperty('type')?data.type:'error',
                            message: data.msg
                        });
                        break;
                    case "closeDialog":
                        this.closeDialog();
                        break;
                    case "closeReload":
                        this.closeDialog();
                        setTimeout(()=>{
                            this.reload();
                        },300);
                        break;
                    case "closeReloadMsg":
                        this.closeDialog();
                        setTimeout(()=>{
                            this.reload();
                            setTimeout(()=>{
                                this.$message({
                                    type: data.hasOwnProperty('type')?data.type:'error',
                                    message: this.$L(data.msg)
                                });
                            },300);
                        },300);
                        break;
                    case "setDialogHeight":
                        this.setDialogHeight(data);
                        break;
                    case "closeDialogLoading":
                        this.closeDialogLoading();
                        break;
                    case "showDialogLoading":
                        this.showDialogLoading();
                        break;
                    case "goto":
                        if (data && data.hasOwnProperty("url")) {
                            window.location.href = data.url;
                        }
                        break;
                    case "router":
                        if (data && data.hasOwnProperty("name")) {
                            this.$router.push({
                                name: data.name,
                                query: data.hasOwnProperty("query") ? data.query : {},
                                params: data.hasOwnProperty("params") ? data.params : {}
                            });
                        }
                        break;
                }
            },
            setDialogHeight(data) {
                if (this.dialogObj && data) {
                    let dialogObj = this.dialogObj;
                    dialogObj.height = data;
                    dialogObj.update = true;
                    this.dialogObj = dialogObj;
                }
            },
            closeDialogLoading() {
                if (this.dialogObj) {
                    let dialogObj = this.dialogObj;
                    dialogObj.loading = false;
                    dialogObj.update = true;
                    this.dialogObj = dialogObj;
                }
            },
            showDialogLoading() {
                if (this.dialogObj) {
                    let dialogObj = this.dialogObj;
                    dialogObj.loading = true;
                    dialogObj.update = true;
                    this.dialogObj = dialogObj;
                }
            },
            closeDialog() {
                if(this.dialogObj.hasOwnProperty('closeFunc') && typeof this.dialogObj.closeFunc === "function"){
                    this.dialogObj.closeFunc();
                }
                this.dialogObj = null;
            },
            doSetup(type,func) {
                const _self = this;
                if (type) {
                    _self.isSetup = false;
                    _self.loading = this.$loading({lock: true});
                    _self.loadConfig(func);
                } else {
                    //设置标题
                    let webName = this.$C('webName');
                    if (webName && webName !== '') {
                        this.seoTitle = webName;
                    }
                    let webIcon = this.$C('webIcon');
                    if (webIcon && webIcon !== '') {
                        this.seoIcon = webIcon;
                    }
                    //读取系统主体颜色
                    let themeColor = this.$themeColor();
                    if (themeColor && themeColor !== '') {
                        document.body.className = "";
                        document.body.classList.add('custom-' + themeColor);
                        let link = document.createElement('link');
                        link.href = process.env.BASE_URL+"theme/"+themeColor+"/index.css";
                        link.rel = 'stylesheet';
                        link.type = 'text/css';
                        document.getElementsByTagName('head')[0].appendChild(link);
                    }
                    if (_self.loading) {
                        _self.loading.close();
                    }
                    _self.isSetup = true;
                    typeof func === "function" && func();
                }
            },
            loadConfig(func) {
                const _self = this;
                //读取服务器配置信息
                _self.$http.get('/uc/server.config.txt').then(function (serverConfigResponse) {
                    let serverConfig = serverConfigResponse.data;
                    //读取网站配置信息
                    _self.$http.get('/uc/web.config.txt').then(function (webConfigResponse) {
                        let webConfig = webConfigResponse.data;
                        if (webConfig && webConfig.hasOwnProperty('systemConfig') && webConfig.hasOwnProperty('langConfig')) {
                            _self.$isPublish(true);
                            _self.$serverUrl(null);
                            _self.$oem(null);
                            localStorage.setItem('VueUserCenterTruePublish','true');
                            serverConfig = _self.$assignObj(serverConfig, webConfig.systemConfig);
                            _self.$store.commit('setServerConfig', {
                                systemConfig: serverConfig,
                                langConfig: webConfig.langConfig
                            });
                            //发布状态，配置信息完整直接进入
                            _self.doSetup(false,func);
                        } else {
                            _self.$store.commit('setServerConfig', {
                                systemConfig: serverConfig,
                            });
                            //非发布状态，需要请求服务器加载配置
                            _self.getServerConfig(func);
                        }
                    }).catch(function (error) {
                        console.log(error);
                        _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                            confirmButtonText: _self.$L('点击重试'),
                            type: 'error',
                            showClose: false
                        }).then(() => {
                            _self.doSetup(true,func);
                        });
                    });
                }).catch(function (error) {
                    console.log(error);
                    _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                        confirmButtonText: _self.$L('点击重试'),
                        type: 'error',
                        showClose: false
                    }).then(() => {
                        _self.doSetup(true,func);
                    });
                });
            },
            getServerConfig(func) {
                const _self = this;
                //读取本地语言包的md5加密字符串，传输到远程服务器，如果一致则使用缓存
                let langPackage = localStorage.getItem('user_center_lang_package');
                if (langPackage) {
                    langPackage = JSON.parse(langPackage);
                    if (typeof langPackage !== "object") {
                        langPackage = {};
                    }
                } else {
                    langPackage = {};
                }
                let langListKey = langPackage && langPackage.hasOwnProperty('key') ? langPackage.key : '';
                //需要请求远程网络
                _self.$api('getConfig', {langListKey: langListKey}, function (reData) {
                    if (reData.flag === 1) {
                        let systemConfig = reData.data.hasOwnProperty('systemConfig') ? reData.data.systemConfig : {};
                        let langConfig = {
                            langList: {},
                            langPackage: {}
                        };
                        if (langListKey !== ''
                            && reData.data.hasOwnProperty('langConfig')
                            && reData.data.langConfig.hasOwnProperty('langPackage')
                            && reData.data.langConfig.langPackage.hasOwnProperty('key')
                            && reData.data.langConfig.langPackage.key === langListKey) {
                            //使用缓存
                            langConfig.langPackage = langPackage && langPackage.hasOwnProperty('package') ? langPackage.package : {};
                        } else {
                            if (reData.data.hasOwnProperty('langConfig')
                                && reData.data.langConfig.hasOwnProperty('langPackage')
                                && reData.data.langConfig.langPackage.hasOwnProperty('package')) {
                                langConfig.langPackage = reData.data.langConfig.langPackage.package;
                                localStorage.setItem('user_center_lang_package', JSON.stringify(reData.data.langConfig.langPackage));
                            }
                        }
                        langConfig.langList = reData.data.hasOwnProperty('langConfig') && reData.data.langConfig.hasOwnProperty('langList') ? reData.data.langConfig.langList : {};
                        _self.$store.commit('setServerConfig', {
                            systemConfig: systemConfig,
                            langConfig: langConfig
                        });
                        _self.doSetup(false,func);
                    } else if (reData.flag === -2) {
                        _self.$alert(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('返回首页'),
                            type: 'error',
                            showClose: false
                        }).then(() => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                            confirmButtonText: _self.$L('点击重试'),
                            type: 'error',
                            showClose: false
                        }).then(() => {
                            _self.doSetup(true,func);
                        });
                    }
                }, function (error) {
                    console.log(error);
                    _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                        confirmButtonText: _self.$L('点击重试'),
                        type: 'error',
                        showClose: false
                    }).then(() => {
                        _self.doSetup(true,func);
                    });
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import './assets/css/common/lib.scss';

    .dialog-body {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 800;
        background-color: rgba(0, 0, 0, 0.51);

        &.show {
            @include show-in(0.6s);
        }

        &.hide {
            @include show-out(0.6s);
        }

        .dialog-box {
            position: absolute;
            background-color: #FFFFFF;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .dialog-title {
                position: relative;
                width: 100%;
                height: 45px;
                overflow: hidden;
                line-height: 45px;
                text-align: left;
                font-size: 16px;
                @extend .no-select;

                .title-txt {
                    margin-left: 20px;
                }

                .title-close {
                    position: absolute;
                    width: 45px;
                    height: 45px;
                    text-align: center;
                    line-height: 45px;
                    right: 0;
                    top: 0;
                    cursor: pointer;
                    @include diy-animation-show(all);

                    &:hover {
                        transform: rotate(90deg);
                    }

                    span {
                        font-size: 20px;
                    }
                }
            }

            .dialog-obj {
                position: relative;
                width: 100%;
            }

            &.mobile {
                width: 100% !important;
                height: 100% !important;

                .dialog-obj {
                    height: calc(100% - 45px) !important;
                    overflow: hidden;
                }
            }
        }
    }
</style>
<style>
    @media screen and (max-width:767px) {
        .el-message-box {
            width: 94% !important;
        }
    }
</style>
